.advantages-list {
	list-style: none;
	margin: 0;
	padding: 0;

	font-family: $primary-font;

	&__description {
		font-size: 20px;
		font-weight: 700;
		margin: 0;
	}

	&__svg {
		margin-bottom: 10px;
	}

	&__item {
		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0;
		}
	}
}